<template>
<div>
<!--    <div v-if="this.store.state.userName==='哥哥' ">-->
    <div>
        <p>
            <i-button  @click="updataweathers" type="primary" >测试获取所有天气</i-button>
        </p>

        <p>
            <i-button @click="checkallweathers" type="primary" >测试check天气</i-button>
        </p>
    </div>
<div v-if="this.store.state.cnName !== null">
    <div>
        {{store.state.cnName}}==欢迎您
    </div>
    <div>
        <input type="test" v-model="newCity.city"  placeholder="输入城市"></input>
        <i-Button type="primary" @click="addCitys" >提交</i-Button>
    </div>
</div>

    <ul >
        <li v-for="(city, index) in citys" v-bind:key="index">
            {{city}} <i-button @click="getDeletedCity(index)">删除</i-button>
        </li>
    </ul>

</div>
</template>

<script>
    import store from '@/store/index'
    export default {
        name: "Weather",
        data(){
            return {
                newCity:{
                    city:'',
                },
                deleteCity:{
                    city:'',
                },
                store,
                citys:[],
                cnName:''
            }
        },
        created () {
            this.getCityList()
        },
        methods: {
            async updataweathers() {
                let res = await this.$Http.updataWeather();
                console.log("更新weathers:" + res)
            },
            async addCitys() {
                let res = await this.$Http.addCity(
                    this.newCity
                );
                this.getCityList();
                console.log(res.data)
            },
            async getCityList() {
                let res = await  this.$Http.getCityList();
                this.citys = res.data;
            },
            async checkallweathers() {
                let res = await this.$Http.checkallweathers();
                console.log("checkallweathers返回:"+res)
            },
            async deletedCity() {
                let res = await this.$Http.deletedCity(
                    this.deleteCity
                );
            },
            getDeletedCity(index) {
                this.deleteCity.city = this.citys[index];
                this.citys.splice(index,1);
                this.deletedCity()
            }
        },
        watch:{
            cnName() {
                this.cnName = this.store.state.cnName
            }
        }
    }
</script>

